/**
 * @author Perlou
 * @table.js
 * @exports {Func} change
 */

'use strict';

var merge = require('./exp2.js').merge;
var ufoFn = require('./workinfo.js').ufoFn;
var myInfoFn = require('./myinfo.js');

function change(){

    var nav = document.getElementsByTagName('nav')[0],
        aA = nav.getElementsByTagName('a'),
        num = 0,
        num2 = 0,
        arr = [document.getElementById('exp'),document.getElementById('work'),document.getElementById('description'),document.getElementById('skill')],
        mp3Sou = document.getElementById('sousou'),
        oExp = document.getElementById('exp'),
        oUl = oExp.getElementsByTagName('ul')[0],
        aChild = oUl.children,
        text = document.getElementById('text'),
        textChild = text.children,
        lightLine = oExp.getElementsByClassName('lightLine')[0],
        bOff = null,
        openNub = null,
        oLi = null;

    for(var i=0; i<aA.length; i++){

        aA[i].index = i;
        aA[i].bOff = true;
        aA[i].onclick = function(){

            for(var i=0; i<aA.length; i++){

                aA[i].className = '';

            }
            this.className = 'active';
            if(this.index!==num) {
                mp3Sou.play();
                arr[num].style.webkitTransform = 'translateZ(1000px)';
                arr[num].style.transform = 'translateZ(1000px)';
                arr[num].style.opacity = 0;
                arr[this.index].style.display = 'block';
                arr[this.index].style.opacity = 1;
                // merge(aChild[openNub]);
                bOff = true;
                oLi = null;
                lightLine.style.display = 'block';
                for(var i=0;i<textChild.length;i++){

                    textChild[i].style.display = 'none';

                }
                num = this.index;
                if (this.bOff) {

                    switch (this.index) {

                        case 1:
                            ufoFn();
                            console.log(1);
                            break;
                        case 2:
                            myInfoFn();
                            console.log(2);
                            break;

                    }
                    this.bOff = false;

                }

                setTimeout(function(){

                    arr[num2].style.webkitTransform = 'translateZ(0px)';
                    arr[num2].style.transform = 'translateZ(0px)';
                    arr[num2].style.zIndex = 1;
                    arr[num2].style.display = 'none';
                    num2 = num;
                    arr[num2].style.zIndex = 3;

                },500);

            }

        }

    }
    aA[0].bOff = false;

}

module.exports = change;


